export default ".set-alignment {\n    padding: 5px 8px;\n    /* word-break: break-all; */\n}\n\nbutton.btn.btn-create {\n    font-size: 23px;\n    padding: 0;\n    margin-right: 4px;\n    color: #3B8BEB!important;\n}\n\n.btn {\n    padding: 0 5px 3px 5px;\n    width: 34px;\n    height: 34px;\n    font-size: 20px;\n    color: #ffffff;\n    margin-right: 10px;\n    margin-bottom: 6px;\n}\n\n.btn-package, .btn-useractive {\n    box-shadow: 0px 5px 6px #28a012;\n    background-color: #28a012;\n}\n\n.btn-note {\n    background-color: #3b8beb;\n    box-shadow: 0px 3px 6px #c4dbf6;\n}\n\n.btn-deactive {\n    background-color: #6d7891;\n    border: 1px solid #e7e3d4;\n    color: #ffffff;\n}\n\n/* table responsive */\n\n@media only screen and (max-width: 760px), (min-device-width: 768px) and (max-device-width: 1024px) {\n    /*\n                      Label the data\n                  You could also use a data-* attribute and content for this. That way \"bloats\" the HTML, this way means you need to keep HTML and CSS in sync. Lea Verou has a clever way to handle with text-shadow.\n                      */\n    td:nth-of-type(1):before {\n        content: \"Date\";\n    }\n    td:nth-of-type(2):before {\n        content: \"Name\";\n    }\n    td:nth-of-type(3):before {\n        content: \"Contact\";\n    }\n    td:nth-of-type(4):before {\n        content: \"Branch\";\n    }\n    td:nth-of-type(5):before {\n        content: \"Package\";\n    }\n}\n\nbutton.btn.btn-guest {\n    background-color: var(--half-bg-fade-color);\n    border: solid 1px var(--half-bg-color);\n    color: var(--half-bg-color);\n    width: 140px;\n    height: 50px;\n    margin-top: 14px;\n    margin-bottom: 0;\n}\n/*# sourceMappingURL=data:application/json;base64, */"