import { Component, OnInit } from '@angular/core';
import { TableListService } from "@core/http/serviceIndex";
import { ActivatedRoute, Router } from "@angular/router";
import { Subscription } from 'rxjs';
import { TableFilter } from '@app/shared/models/table-filter';

@Component({
  selector: 'app-user-list',
  templateUrl: './user-list.component.html',
  styleUrls: ['./user-list.component.css']
})
export class UserListComponent implements OnInit {
  public selectedTab: number = 0;
  public tableFilter = <TableFilter>{
    page: 1,
    limit: 10,
    sortColumn: 'asc',
    sortDirection: 'createdDate',
  };

  public subscription: Subscription;

  public userLists: any = [];

  public isAsc: boolean = true;
  public dialog: any = {
    showText: "",
    isDelete: false,
    isWarning: true,
    isconfirmBtn: true,
    isPackage: false,
    getCode: false
  }
  public isLoading: boolean = true;
  public activeUser: any;
  public isShow: boolean = false;
  public isToast: boolean = false;
  public isErrText: any;

  constructor(
    private _router: Router, private _tableService: TableListService, private _activeRoute: ActivatedRoute) { }

  ngOnInit() {
    this.isLoading = true;
    this._activeRoute.queryParams.subscribe(
      (params) => {
        this.tableFilter.search = '';
        if (params.page) {
          this.tableFilter.page = params.page;
          this.tableFilter.limit = params.limit;
          // this.tableFilter.search="";
        } else if (params.search) {
          this.tableFilter.search = params.search;
        }
        this.getAllUsers();
      }
    )

  }



  getAllUsers() {
    this.isLoading = true;
    this.subscription = this._tableService.getUserList(this.tableFilter).subscribe(
      (res: any) => {
        this.userLists = res.userList;
        this.tableFilter = res.filter;
        this.isLoading = false;
      });
  }

  deleteUser(e) {
    this.subscription = this._tableService.onDeleteUser(e.userId).subscribe(
      (res: any) => {
        this.getAllUsers();
      });

  }



  fitter(sortColumn) {
    this.isLoading = true;
    this.tableFilter.sortDirection = this.isAsc ? 'asc' : 'desc';
    this.isAsc = !this.isAsc ? this.isAsc = true : this.isAsc = false;
    this.tableFilter.sortColumn = sortColumn;
    this.getAllUsers();
  }

  public expandId: any;
  expand(userId) {
    this.expandId = userId == this.expandId ? undefined : userId;

  }

  goToRoute(userId) {
    this._router.navigate(['notes', userId], { relativeTo: this._activeRoute })
  }



  showDialog(uId) {
    this.activeUser = uId;
    this.dialog.showText = uId.status == "ACTIVE" ? "Are you sure deactive this user?" : "Are you sure active this user?";
    this.isShow = true;
  }


  onDialog(e) {
    if (e) {
      this.isLoading = true;
      let temp = {
        "status": this.activeUser.status == "ACTIVE" ? "DEACTIVE" : "ACTIVE"
      }
      this.subscription = this._tableService.deactiveUser(this.activeUser.userId, temp).subscribe(
        (res: any) => {
          this.getAllUsers();
          this.isToast = true;
          this.isErrText = res.message;
        }
      );
    }
    this.isShow = false;
  }

  clickPackage(user) {
    this.activeUser = user;
    this.isShow = true;
    this.dialog.isconfirmBtn = false;
    this.dialog.showText = this.activeUser.package.name;
  }



  ngOnDestroy(): void {
    if (this.subscription) this.subscription.unsubscribe();
  }


  goToGuest() {
    this._router.navigate(['guestlists'], { relativeTo: this._activeRoute })
  }
}
