import { ActivatedRouteSnapshot, Resolve } from '@angular/router';
import { Injectable } from '@angular/core';
import { DetailService } from '@app/core/http/detail.service';
import { forkJoin, Observable } from 'rxjs';

@Injectable()
export class UserCreateResolver implements Resolve<any> {

    constructor(private detail: DetailService) {
    }

    resolve(route: ActivatedRouteSnapshot):
        Observable<any> {
        let userId, branch, desirescore, pack, userDetail;

        userId = route.params['userId'];
        branch = this.detail.getAllBranch();
        desirescore = this.detail.getdesiredScore();
        pack = this.detail.getAllPackage();
        if (userId) {
            userDetail = this.detail.getDetail(`users/${userId}`);
           return forkJoin([branch, desirescore, pack, userDetail]);
        } else {
            return forkJoin([branch, desirescore, pack]);
        }


    }
}